import { autorun } from "mobx";
import { observer } from "mobx-react";
import * as React from "react";
import { getLoadedMonaco } from "../../../monaco-loader";
import { IPlaygroundProject, IPreviewState } from "../../../shared";
import { Page } from "../../components/Page";
import { Select } from "../../components/Select";
import {
	MonacoEditor,
	MonacoEditorHeight,
} from "../../components/monaco/MonacoEditor";
import { withLoadedMonaco } from "../../components/monaco/MonacoLoader";
import { hotComponent } from "../../utils/hotComponent";
import { IReference, ref } from "../../utils/ref";
import { getNpmVersionsSync } from "./getNpmVersionsSync";
import { getPlaygroundExamples, PlaygroundExample } from "./playgroundExamples";
import { PlaygroundModel } from "./PlaygroundModel";
import { Preview } from "./Preview";
import { SettingsDialog } from "./SettingsDialog";

@hotComponent(module)
@observer
export class PlaygroundPageContent extends React.Component<
	{ model: PlaygroundModel },
	{}
> {
	render() {
		const model = this.props.model;

		return (
			<Page>
				<SettingsDialog model={model} />
				<div className="p-2" style={{ height: "100%" }}>
					<Horizontal>
						<Vertical>
							<div style={{ flex: 1 }}>
								<LabeledEditor
									label="JavaScript"
									titleBarItems={
										<div
											className="hstack"
											style={{ marginLeft: "auto" }}
										>
											<span style={{ marginRight: 8 }}>
												Example:
											</span>
											<Select<PlaygroundExample>
												values={getPlaygroundExamples().map(
													(e) => ({
														groupTitle:
															e.chapterTitle,
														items: e.examples,
													})
												)}
												value={ref(
													model,
													"selectedExample"
												)}
												getLabel={(i) => i.title}
											/>
										</div>
									}
								>
									<Editor
										language={"javascript"}
										value={ref(model, "js")}
									/>
								</LabeledEditor>
							</div>

							<div>
								<LabeledEditor label="HTML">
									<Editor
										height={{
											kind: "dynamic",
											maxHeight: 200,
										}}
										language={"html"}
										value={ref(model, "html")}
									/>
								</LabeledEditor>
							</div>

							<div>
								<LabeledEditor label="CSS">
									<Editor
										height={{
											kind: "dynamic",
											maxHeight: 200,
										}}
										language={"css"}
										value={ref(model, "css")}
									/>
								</LabeledEditor>
							</div>
						</Vertical>

						<LabeledEditor
							label="Preview"
							titleBarItems={
								<div
									style={{ marginLeft: "auto" }}
									className="d-flex gap-2"
								>
									{model.settings.settings.monacoSource ===
										"npm" && (
										<Select
											values={getNpmVersionsSync(
												model.settings.settings
													.npmVersion
											)}
											getLabel={(i) => i}
											value={{
												get() {
													return model.settings
														.settings.npmVersion;
												},
												set(value) {
													model.settings.setSettings({
														...model.settings
															.settings,
														npmVersion: value,
													});
												},
											}}
										/>
									)}

									<button
										type="button"
										className="btn btn-light settings bi-gear"
										style={{ fontSize: 20 }}
										onClick={() =>
											model.showSettingsDialog()
										}
									/>
								</div>
							}
						>
							<Preview model={model} />
						</LabeledEditor>
					</Horizontal>
				</div>
			</Page>
		);
	}
}

export class LabeledEditor extends React.Component<{
	label: string;
	children: React.ReactNode;
	titleBarItems?: React.ReactNode;
}> {
	render() {
		return (
			<Vertical>
				<Horizontal style={{ height: undefined }}>
					<div style={{ margin: "5px 10px" }}>{this.props.label}</div>
					{this.props.titleBarItems}
				</Horizontal>
				<div style={{ flex: 1 }}>{this.props.children}</div>
			</Vertical>
		);
	}
}

@withLoadedMonaco
class Editor extends React.Component<{
	language: string;
	value: IReference<string>;
	height?: MonacoEditorHeight;
}> {
	private editor: monaco.editor.IStandaloneCodeEditor | undefined = undefined;
	private disposables: monaco.IDisposable[] = [];

	private readonly model = getLoadedMonaco().editor.createModel(
		this.props.value.get(),
		this.props.language
	);

	render() {
		return (
			<MonacoEditor
				model={this.model}
				onEditorLoaded={(editor) => this.initializeEditor(editor)}
				height={this.props.height}
				className="editor-container"
			/>
		);
	}

	private ignoreChange = false;

	initializeEditor(editor: monaco.editor.IStandaloneCodeEditor) {
		this.editor = editor;
		this.disposables.push(this.editor);
		this.disposables.push(
			this.editor.onDidChangeModelContent((e) => {
				this.ignoreChange = true;
				try {
					this.props.value.set(this.editor!.getValue());
				} finally {
					this.ignoreChange = false;
				}
			})
		);

		this.disposables.push({
			dispose: autorun(
				() => {
					const value = this.props.value.get();
					if (!this.ignoreChange) {
						this.editor!.setValue(value);
					}
				},
				{ name: "update text" }
			),
		});
	}

	componentWillUnmount() {
		this.disposables.forEach((d) => d.dispose());
	}
}

export function Vertical(props: { children: React.ReactNode }) {
	return (
		<div
			style={{
				display: "flex",
				flexDirection: "column",
				alignItems: "stretch",
				width: "100%",
				height: "100%",
			}}
		>
			{props.children}
		</div>
	);
}

export function Horizontal(props: {
	children: React.ReactNode;
	style?: React.CSSProperties;
}) {
	return (
		<div
			style={{
				display: "flex",
				flexDirection: "row",
				alignItems: "stretch",
				height: "100%",
				width: "100%",
				...props.style,
			}}
		>
			{props.children}
		</div>
	);
}
